export default {
	data() {
		return {
			id: '',
			playinfo: ''
		}
	},
	onLoad(e) {
		this.id = e.id
		this.getTopList()
	},
	methods: {
		getTopList () {
			let _this = this;
			let params = {
				idx : this.id
			}
			_this.MusicApi.request('top/list',params, 'get').then(res => {
				_this.playinfo = res.data.playlist
			})
		}
	}
}
